/* intro *
*********/
.b-intro {
  position: relative;
  width: 100%;
  margin: auto;
  text-align: center; }

.intro-media {
  position: relative;
  margin: auto;
  margin-bottom: 20px;
  width: 60px; }
  @media (max-width: 400px) {
    .intro-media {
      width: 50px; } }

.intro-title {
  font-size: 52px;
  font-family: 'Gingham Variable', BlinkMacSystemFont , sans-serif;
  font-variation-settings: "wght" 800, "wdth" 150;
  color: #5B5B5B; }
  @media (max-width: 768px) {
    .intro-title {
      font-size: 38px; } }
  @media (max-width: 400px) {
    .intro-title {
      font-size: 24px; } }

.intro-text {
  font-family: 'Gingham Variable', BlinkMacSystemFont , sans-serif;
  font-variation-settings: "wght" 800, "wdth" 150;
  font-size: 62px;
  color: #2A2A2A; }
  @media (max-width: 768px) {
    .intro-text {
      font-size: 46px; } }
  @media (max-width: 400px) {
    .intro-text {
      font-size: 30px; } }

/* icon *
********/
.b-icon {
  width: 100%; }

.icon-inner {
  fill: #5B5B5B; }



