/* page *
********/
.l-page {
  width: 100%;
  height: 100%; }

.page-inner {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%; }

.page-header {
  width: 100%;
  background: #50E3C2; }

.page-header-inner {
  display: -ms-flexbox;
  display: flex;
  padding: 0 15px;
  min-height: 275px; }

.page-main {
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  width: 100%; }

.page-section {
  position: relative;
  width: 100%; }

.page-section-inner {
  max-width: 400px;
  margin: auto;
  padding: 75px 25px; }
  @media (max-width: 768px) {
    .page-section-inner {
      padding: 50px 25px; } }
  @media (max-width: 400px) {
    .page-section-inner {
      padding: 25px 25px; } }

.page-footer {
  -ms-flex-negative: 0;
      flex-shrink: 0;
  width: 100%;
  background: #ededed; }

.page-footer-inner {
  padding: 25px; }

.page-footer-text {
  font-family: 'Gingham Variable', BlinkMacSystemFont , sans-serif;
  font-variation-settings: "wght" 300, "wdth" 250;
  font-size: 14px;
  color: #5B5B5B; }
  @media (max-width: 400px) {
    .page-footer-text {
      font-size: 12px; } }



